<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <data-table :titles="titles" :collection="groups">
    <template #filters>
      <div class="py-3">
        <div class="grid grid-cols-1">
          <div class="block">
            <label for="textSearch">{{ $t("attributes.name") }}</label>
            <div class="w-full mt-1.5">
              <input
                v-model="groups.filters.search"
                id="textSearch"
                type="text"
                :placeholder="$t('placeholder.search')"
                class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              />
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #body>
      <tr v-for="group in groups" :key="group.id">
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ group.id }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ group.name }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ group.workspace_name }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ group.size }}
        </td>
        <td>
          <div class="flex space-x-2">
            <router-link
              :to="{ name: 'app.group.survey', params: { id: group.id } }"
              :title="$t('titles.survey')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-warning hover:bg-warning-focus focus:bg-warning-focus active:bg-warning-focus/90"
            >
              <v-icon name="user" size="5"></v-icon>
            </router-link>

            <router-link
              :to="{ name: 'app.group.surveys', params: { id: group.id } }"
              :title="$t('titles.surveys')"
              class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-info hover:bg-info-focus focus:bg-info-focus active:bg-info-focus/90"
            >
              <v-icon name="clipboard-document-check" size="5"></v-icon>
            </router-link>

            <v-button
              color="error"
              icon
              :size="10"
              :title="$t('labels.delete')"
              @click="deleteModal(group)"
            >
              <v-icon name="archive-delete" size="5"></v-icon>
            </v-button>
          </div>
        </td>
      </tr>
    </template>
  </data-table>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="error" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import GroupCollection from "@/collections/GroupCollection";
import ModelRequest from "@/request/ModelRequest";

export default {
  name: "GroupList",
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.groups"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        { column: "name", title: this.$t("attributes.group"), sortable: true },
        {
          column: "workspace_id",
          title: this.$t("attributes.workspace"),
          sortable: true,
        },
        { column: "size", title: this.$t("attributes.size"), sortable: true },
        { title: this.$t("labels.actions") },
      ],
      groups: new GroupCollection(),
      deleteSelect: null,
    };
  },
  created() {
    this.groups.fetchWithFilters();
  },
  methods: {
    deleteModal(model) {
      this.deleteSelect = model;
      this.$refs.modal.toggle();
    },
    deleteData() {
      const request = new ModelRequest(this.deleteSelect);

      request.delete().then(() => {
        this.$refs.modal.toggle();

        this.groups.fetchWithFilters();
      });
    },
  },
};
</script>